.ToastMessage {
    display: flex;
    position: fixed;
    bottom: 2em;
    left: 0;
    flex-direction: column;
    gap: 1em;
    padding: 1em;
}

.ToastMessage>.message {
    padding: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 1em;
    background-color: var(--back-color, #ccc);
    color: var(--color, #000);
    border: none;
    border-radius: 10px;

}