.LoadingScreen {
    display: none;
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(1);
    position: fixed;
    justify-content: center;
    align-items: center;
    font-size: 5em;
    background-color: #181818;
    z-index: 10;
    top: 0;
    left: 0;
}

.LoadingScreen>i {
    animation: rotate 1.5s ease-in-out infinite;
    color: aqua;
}

@keyframes rotate {
    0% {
        rotate: 0deg;
    }

    100% {
        rotate: 360deg;
    }
}